<template>
  <v-layout>
    <v-container>
      <add-project v-on:getVal="getVal" />
      <center>
        <v-img
          v-if="!isGetData && !projects.length"
          class="image-not-fond"
          contain
          :src="'/nodata.png'"
        >
        </v-img>
        <v-progress-circular
          id="progress"
          v-if="isGetData"
          :size="70"
          width="7"
          color="primary"
          indeterminate
        ></v-progress-circular>
      </center>

      <v-row>
        <card-projects
          v-on:getVal="getVal"
          v-for="(project, index) in this.$store.getters.projects"
          :key="index"
          :dataProject="project"
        />
      </v-row>
    </v-container>
  </v-layout>
</template>

<script>
import { projectController } from "../../components/projects/project";
export default {
  mounted() {
    projectController.initial();
    this.getProjects();
  },
  data() {
    return {
      isGetData: false,

      // projects it up in car component
    };
  },
  computed: {
    projects() {
      return this.$store.getters.projects;
    },
  },
  components: {
    "add-project": () => import("../../components/projects/add.vue"),
    "card-projects": () => import("../../components/projects/card.vue"),
  },
  methods: {
    getVal() {
      this.$store.commit("SET_PROJECTS", []);
      this.getProjects();
    },
    getProjects: async function () {
      this.isGetData = true;
      await projectController.getProjects();
      this.isGetData = false;
    },
  },
};
</script>

<style>
#progress {
  margin-top: 20%;
  z-index: 1023;
}
.image-not-fond {
  margin-top: 5%;
  height: 500px;
  width: 500px;
}
</style>